import React, { Fragment } from "react";
import { GoogleMap, withGoogleMap, withScriptjs } from "react-google-maps";
import MarkerClusterer from "react-google-maps/lib/components/addons/MarkerClusterer";
import { compose, withHandlers, withProps, withStateHandlers } from "recompose";
import HotelMapMarkerCluster from "./ListingPageMap";
import HotelMapMarkerSingle from "./SinglePageMap";

export const MapLoadingElement = (mapLoadingProps) => {
  const { height } = mapLoadingProps;
  return <div style={{ height: height }} />;
};

export const mapRenderData = {
  googleMapURL: process.env.REACT_APP_GOOGLE_MAP_API_KEY,
  loadingElement: <MapLoadingElement height="100%" />,
  containerElement: <div style={{ height: `400px` }} />,
  mapElement: <div style={{ height: `400px` }} />,
};

export const MapDataProcessing = compose(
  withProps(mapRenderData),
  withHandlers({
    onMarkerClustererClick: () => (markerClusterer) => {
      const clickedMarkers = markerClusterer.getMarkers();
    },
  }),
  withStateHandlers(
    () => ({
      isOpen: false,
    }),
    {
      infoWindowToggle: ({ isOpen }) => (index) => ({
        isOpen: !isOpen,
        markerIndex: index,
      }),
    }
  ),
  withScriptjs,
  withGoogleMap
)((props) => {
  const { multiple, mapCenter } = props;
  return (
    <Fragment>
      {props.cstmMap === true ? (
        multiple && multiple === true ? (
          <GoogleMap
            defaultZoom={4}
            defaultCenter={{
              lat: 39.828175,
              lng: -98.5795,
            }}
          >
            <MarkerClusterer
              onClick={props.onMarkerClustererClick}
              averageCenter
              defaultEnableRetinaIcons={true}
              gridSize={60}
            >
              <HotelMapMarkerCluster {...props} />
            </MarkerClusterer>
          </GoogleMap>
        ) : (
          <GoogleMap
            defaultZoom={4}
            defaultCenter={{
              lat: 39.828175,
              lng: -98.5795,
            }}
          >
            <HotelMapMarkerSingle {...props} />
          </GoogleMap>
        )
      ) : (
        <GoogleMap
          defaultZoom={4}
          defaultCenter={{
            lat: 39.828175,
            lng: -98.5795,
          }}
        >
          <MarkerClusterer
            onClick={props.onMarkerClustererClick}
            averageCenter
            defaultEnableRetinaIcons={true}
            gridSize={60}
          >
            <HotelMapMarkerCluster {...props} />
          </MarkerClusterer>
        </GoogleMap>
      )}
    </Fragment>
  );
});

const Map = (props) => {
  return <Fragment>{props.children}</Fragment>;
};

export default Map;
